


    .tasks__item {
        display: grid;
        grid-template-columns:auto 1fr auto ;
        grid-gap: 10px;   
        align-items:center;
        padding:10px;
        border-bottom: 1px solid rgb(198, 196, 196);
    
    }

    

   

.tasks__content{
    text-decoration:none;
}


